@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap);
*{outline-width: 0px; border: none;}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#homepage{padding:20px;padding-bottom:0px;min-height:100vh;background:url(/course-problem-deck/static/media/background.5023a7e0.jpg) no-repeat center center fixed;background-size:cover;font-family:'Libre Baskerville', serif}#homepage .header{text-align:center;color:white}#homepage .header h1{margin-bottom:0px}#homepage .course{display:flex;flex-direction:column;flex-wrap:wrap;margin:50px auto}#homepage .course>div{box-shadow:0 2.8px 2.2px rgba(0,0,0,0.034),0 6.7px 5.3px rgba(0,0,0,0.048),0 12.5px 10px rgba(0,0,0,0.06),0 22.3px 17.9px rgba(0,0,0,0.072),0 41.8px 33.4px rgba(0,0,0,0.086),0 100px 80px rgba(0,0,0,0.12);opacity:0.8;width:50vw;margin:10px auto;padding:20px;background:white;border-radius:5px}#homepage .course .course-content h1{margin:0px;padding:0px;font-size:25px}#homepage .course .course-content .instructor{margin:0px;padding:0px;font-size:15px}#homepage .course .course-content .subtitle{margin-bottom:30px}#homepage .course .course-content a{padding:10px;text-decoration:none;color:white;border-radius:5px}#homepage .course .course-content .browse-course{background:#17a2b8;margin-right:10px}#homepage .course .course-content .browse-problems{background:#28a745}#homepage .fotter{text-align:center;color:white;font-size:20px;margin-bottom:10px}#homepage .fotter a{color:white !important;text-decoration:none}#homepage .fotter span{color:red}

.fotter{text-align:center;color:white;font-size:20px;padding:10px}.fotter a{color:white !important;text-decoration:none}.fotter span{color:red}

#course-page{padding:20px;padding-bottom:0px;background:url(/course-problem-deck/static/media/background.5023a7e0.jpg) no-repeat center center fixed;background-size:cover;min-height:100vh;font-family:'Libre Baskerville', serif}#course-page .header{text-align:center;color:white}#course-page .header h1{margin-bottom:0px;text-transform:uppercase}#course-page .problems .problem{background-color:white;padding:10px;margin:10px;opacity:0.8}#course-page .problems .problem span{color:#155724 !important;font-size:20px;margin-right:10px}#course-page .problems .problem:hover{background-color:honeydew}#course-page .problems .problem-title{color:black;cursor:pointer;text-decoration:none !important;font-size:20px}#course-page .problems .problem-title:hover{color:#1BA94C}#course-page .problems .filter-box{background-color:white;padding:10px;margin:10px;opacity:0.8}#course-page .problems .filter-input{font-size:20px;width:100%;font-family:'Libre Baskerville', serif}#course-page .problems .filter-item{margin-right:10px;margin-bottom:10px;border:1px solid black;border-radius:10px;padding:5px;display:inline-block}#course-page .problems .filter-item>button{background-color:transparent;cursor:pointer}#course-page .error-message{font-size:20px;text-align:center;color:#f8d7da;margin:50px;padding:10px}#course-page .empty-message{font-size:20px;color:white;text-align:center;margin:50px auto}

#loader1{text-align:center;height:300px;display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;color:white;font-size:20px}#loader1 .lds-facebook{display:block;position:relative;width:100px;height:100px}#loader1 .lds-facebook div{display:inline-block;position:absolute;left:8px;width:16px;background:#fff;animation:lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite}#loader1 .lds-facebook div:nth-child(1){left:8px;animation-delay:-0.24s}#loader1 .lds-facebook div:nth-child(2){left:32px;animation-delay:-0.12s}#loader1 .lds-facebook div:nth-child(3){left:56px;animation-delay:0}@keyframes lds-facebook{0%{top:8px;height:64px}50%,100%{top:24px;height:32px}}

#login{padding:50px;font-family:'Libre Baskerville', serif}#login .flash-message{display:flex;align-items:center;justify-content:space-between;padding:5px;border-radius:5px}#login .flash-message button{border:none;background:transparent}#login .error{color:#842029;background:#f5c2c7}#login .error button{color:#842029}#login .success{color:#0f5132;background:#badbcc}#login .success button{color:#0f5132}#login .form-group{margin:20px auto}#login .form-group label{display:block;margin:10px auto}#login .form-group input{border:1px solid #ced4da;width:-webkit-fill-available;padding:10px;font-size:20px;border-radius:5px;font-family:'Libre Baskerville', serif}#login .form-group input:focus{outline-width:2px;outline-color:#92a8d1}#login button{padding:10px;font-size:20px;cursor:pointer;background:#007bff;color:white;border-radius:5px;font-family:'Libre Baskerville', serif}

#loader2 .lds-heart{display:inline-block;position:relative;width:80px;height:80px;transform:rotate(45deg);transform-origin:40px 40px}#loader2 .lds-heart div{top:32px;left:32px;position:absolute;width:32px;height:32px;background:red;animation:lds-heart 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1)}#loader2 .lds-heart div:after,#loader2 .lds-heart div:before{content:" ";position:absolute;display:block;width:32px;height:32px;background:red}#loader2 .lds-heart div:before{left:-24px;border-radius:50% 0 0 50%}#loader2 .lds-heart div:after{top:-24px;border-radius:50% 50% 0 0}@keyframes lds-heart{0%{transform:scale(0.95)}5%{transform:scale(1.1)}39%{transform:scale(0.85)}45%{transform:scale(1)}60%{transform:scale(0.95)}100%{transform:scale(0.9)}}

#attempt-problem{-ms-user-select:none;user-select:none;-webkit-user-select:none;display:flex;justify-content:space-between;overflow:hidden;height:100vh;width:100vw}#attempt-problem>div{height:100vh;overflow:scroll}#attempt-problem .resizer{width:5px !important;cursor:ew-resize;border:2px solid transparent;overflow:hidden !important}#attempt-problem .left,#attempt-problem .right{min-width:30vw;width:50vw}#attempt-problem .full-flex{flex:1 1}#attempt-problem .left{font-family:'Libre Baskerville', serif;padding:10px}#attempt-problem .left a{color:black !important}#attempt-problem .left .problem-header{font-weight:bold;font-size:30px}#attempt-problem .left .problem-score{text-transform:uppercase}#attempt-problem .left .tags .tags-header{font-size:18px;font-weight:bold}#attempt-problem .left .tags .tag{display:inline-block;background-color:whitesmoke;border-radius:10px;padding:5px;margin:5px 5px 0px 0px}#attempt-problem .problem{margin:50px auto}#attempt-problem .problem .ql-toolbar{display:none !important}#attempt-problem .problem .ql-container.ql-snow{border:none !important;font-family:'Libre Baskerville', serif}#attempt-problem .problem .ql-editor{padding:0px !important}#attempt-problem .problem pre.ql-syntax{color:black !important;background-color:white !important;border:1px solid darkgray}#attempt-problem .right{flex:1 1}#attempt-problem .right .code-operations{text-align:right;font-family:'Libre Baskerville', serif;font-size:18px;margin:10px}#attempt-problem .right .code-operations>button{font-size:18px;background:transparent;font-family:'Libre Baskerville', serif;cursor:pointer}#attempt-problem .right .code-operations .button-container{display:flex;align-items:center;justify-content:flex-end;margin:10px 0px}#attempt-problem .right .code-operations .button-container .submit-code{background:#28a745;cursor:pointer;color:white;border-radius:5px;padding:10px;font-family:'Libre Baskerville', serif;font-size:18px}#attempt-problem .right .custom-test{font-family:'Libre Baskerville', serif;font-size:15px;padding:10px;padding-left:0px;margin:5px;margin-left:0px}#attempt-problem .right .custom-test>textarea,#attempt-problem .right .custom-test>input{border:1px solid black;border-radius:5px;width:95%;padding:10px;font-family:'Libre Baskerville', serif;font-size:15px;margin:5px auto}#attempt-problem .right .custom-test>textarea{height:100px}#attempt-problem .right .custom-test textarea:focus,#attempt-problem .right .custom-test input:focus{border-color:#9ecaed;box-shadow:0 0 2px #9ecaed}#attempt-problem .right .custom-test .error-result{background:#f8d7da;color:#842029;padding:10px;margin-bottom:20px}#attempt-problem .right .custom-test .success-result{background:#d1e7dd;color:#0f5132;padding:10px;margin-bottom:20px}#attempt-problem .right .results{border:1px solid black;padding:5px;display:flex}#attempt-problem .right .results .test-case-list .test-case{background:whitesmoke;cursor:pointer;font-family:'Libre Baskerville', serif;font-size:18px;padding:5px 10px;width:200px !important;display:flex;align-items:center;justify-content:space-between}#attempt-problem .right .results .test-case-list .test-case .rejected{color:#842029}#attempt-problem .right .results .test-case-list .test-case .accepted{color:#0f5132}#attempt-problem .right .results .test-case-list .selected-test-case{background:white;box-shadow:0 2px 2px 2px gainsboro;-webkit-box-shadow:0 2px 2px 2px gainsboro}#attempt-problem .right .results .test-case-list .user-score{font-family:'Libre Baskerville', serif;font-size:18px;padding:5px}#attempt-problem .right .results .test-case-result{flex:1 1;font-family:'Libre Baskerville', serif;font-size:18px;padding:0px 10px 10px 10px}#attempt-problem .right .results .test-case-result textarea{font-family:'Libre Baskerville', serif;font-size:15px;width:100%;background:whitesmoke;padding:5px;overflow:scroll;resize:none;min-height:80px;margin-bottom:20px}.react-toast-notifications__toast__content{font-family:'Libre Baskerville', serif !import