.loader0{background:#2c3e50;height:100vh;width:100vw;display:flex;align-items:center;justify-content:center;flex-direction:column;font-family:'Libre Baskerville', serif}.loader0 .sk-cube-grid{width:60px;height:60px;margin:0px auto}.loader0 .sk-cube-grid .sk-cube{width:33%;height:33%;background-color:white;float:left;animation:sk-cubeGridScaleDelay 1.3s infinite ease-in-out}.loader0 .sk-cube-grid .sk-cube1{animation-delay:0.2s}.loader0 .sk-cube-grid .sk-cube2{animation-delay:0.3s}.loader0 .sk-cube-grid .sk-cube3{animation-delay:0.4s}.loader0 .sk-cube-grid .sk-cube4{animation-delay:0.1s}.loader0 .sk-cube-grid .sk-cube5{animation-delay:0.2s}.loader0 .sk-cube-grid .sk-cube6{animation-delay:0.3s}.loader0 .sk-cube-grid .sk-cube7{animation-delay:0s}.loader0 .sk-cube-grid .sk-cube8{animation-delay:0.1s}.loader0 .sk-cube-grid .sk-cube9{animation-delay:0.2s}@keyframes sk-cubeGridScaleDelay{0%,70%,100%{transform:scale3D(1, 1, 1)}35%{transform:scale3D(0, 0, 1)}}.loader0 .message{color:white;text-align:center;margin-top:20px;font-size:20px}.loader0 .message a{color:white !important;text-decoration:none}

#loader3{display:inline-block;margin-left:10px}#loader3 .lds-ring{display:inline-block;position:relative;width:20px;height:20px;top:5px}#loader3 .lds-ring div{box-sizing:border-box;display:block;position:absolute;width:15px;height:15px;margin:2px;border:2px solid black;border-radius:50%;animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:black transparent transparent transparent}#loader3 .lds-ring div:nth-child(1){animation-delay:-0.45s}#loader3 .lds-ring div:nth-child(2){animation-delay:-0.3s}#loader3 .lds-ring div:nth-child(3){animation-delay:-0.15s}@keyframes lds-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.modal{position:fixed;z-index:1000;height:100vw;width:100vw}.modal .modal-content{background:white;display:flex;flex-direction:column;opacity:1.0;font-family:'Libre Baskerville', serif;font-size:20px;width:50vw;border-radius:10px;margin:10px auto}.modal .modal-content>div{padding:10px}.modal .modal-content .modal-header{display:flex;align-items:center;justify-content:space-between;border-bottom:1px solid gainsboro}.modal .modal-content .modal-header svg{cursor:pointer}.modal .modal-content .modal-body{display:flex;align-items:center;justify-content:center}.modal .modal-content .modal-body img{width:200px}.modal .modal-content .modal-body .message{font-size:18px;margin-bottom:20px}.modal .modal-content .modal-body button{background:transparent;border:none;outline-width:0px;cursor:pointer;color:white;font-size:20px;font-family:'Libre Baskerville', serif;padding:10px;margin-right:10px;border-radius:5px}.modal .modal-content .modal-body .danger{background:#dc3543}.modal .modal-content .modal-body .success{background:#28a745}.modal .drop-animation{animation:drop-animation 1s forwards}@keyframes drop-animation{0%{transform-origin:center;opacity:0.1}20%{transform:translate3d(0, 10px, 0);opacity:0.2}40%,45%{transform:translate3d(0, 10px, 0);opacity:0.5}to{opacity:1;transform:translate3d(0, 50px, 0)}}.modal .box-shadow{box-shadow:0 3px 7px rgba(0,0,0,0.3)}

